import {Row, Col, Badge} from 'react-bootstrap'
import React from 'react';
import './components.css'

const THIS_AUTHOR = 'Shayan Monadjemi'

function Publication(props) {
    let authors = props.publication.authors.split(', ')

    return (
        <>
            <Row className='resume-comp'>
                <Col lg={12}>
                    <div>
                        {props.publication.title}
                    </div>

                    <div className='text-muted'>{authors.map((a, i) => {
                        let delim = ', '
                        if (i === authors.length - 1) {
                            delim = ''
                        }

                        if (a === THIS_AUTHOR)
                            return <><b>{a}</b>{delim}</>
                        else
                            return <>{a}{delim}</>
                    })}</div>

                    <div className='text-muted'><em>{props.publication.venue}</em></div>

                    <>
                        {props.publication.tags.map(t => {
                            return <><Badge variant={'secondary'}>{t}</Badge> &nbsp;</>
                        })}

                        {Object.keys(props.publication.links).map(t => {
                            return <><a className={'small'} href={props.publication.links[t]} target={'_blank'} variant={'secondary'}>{t}</a> &nbsp;</>
                        })}

                    </>
                </Col>
            </Row>
        </>
    )
}

export default Publication;